  const getChildrenList = (children) => {
    let childrenList = [];
    if ((children || []).length > 0) {
      children.forEach((obj) => {
        let attributes = {
          " ": obj?.relation?.value || "",
          "  ": obj.dob,
          " ": obj?.qualification?.value,
          // " ": obj?.occupation?.value
        };
        if (!obj.isAlive) {
          attributes = {
            ...attributes,
            "  ": obj.dod,
          };
        }

        let spouse = obj?.spouse;

        let spouseAttributes = {
          " ": spouse?.relation?.value || "",
          "  ": spouse?.dob,
          " ": spouse?.qualification?.value,
          // " ": spouse?.occupation?.value
        };
        if (!spouse?.isAlive) {
          spouseAttributes = {
            ...spouseAttributes,
            "  ": spouse?.dod,
          };
        }

        spouse = {
          name: `${spouse?.firstName} ${spouse?.middleName} ${spouse?.lastName}`,
          attributes: spouseAttributes,
        };

        if (obj?.spouse) {
          childrenList.push({
            name: `${obj.id})${obj?.firstName} ${obj?.middleName} ${obj?.lastName}`,
            attributes,
            children: [spouse, ...getChildrenList(obj?.children || [])],
          });
        } else {
          childrenList.push({
            name: `${obj.id}) ${obj?.firstName} ${obj?.middleName} ${obj?.lastName}`,
            attributes,
            children: getChildrenList(obj?.children || null),
          });
        }
      });
    }
    return childrenList.length > 0 ? childrenList : "";
  };
